import React from 'react';
import { Bar } from 'react-chartjs-2';



const Pokemonstat = ({stats}) => {
    const labels= [];
    const values = [];
    stats.map(stat =>{
        labels.push(stat.stat.name);
        values.push(stat.base_stat);
    })
    const data = {
        labels,
        datasets: [
            {
                data: values,
                backgroundColor: [
                    'rgba(255, 99, 132, 0.2)',
                    'rgba(54, 162, 235, 0.2)',
                    'rgba(255, 206, 86, 0.2)',
                    'rgba(75, 192, 192, 0.2)',
                    'rgba(153, 102, 255, 0.2)',
                    'rgba(255, 159, 64, 0.2)',
                ],
                borderColor: [
                    'rgba(255, 99, 132, 1)',
                    'rgba(54, 162, 235, 1)',
                    'rgba(255, 206, 86, 1)',
                    'rgba(75, 192, 192, 1)',
                    'rgba(153, 102, 255, 1)',
                    'rgba(255, 159, 64, 1)',
                ],
                borderWidth: 1,
            },
        ],
    };

    const options = {
        plugins:{
            legend: {
                display: false
            },
        },
        scales: {
            yAxes: [
                {
                    ticks: {
                        beginAtZero: true,
                    },
                },
            ],
        },
    };
    return (
        <>
            <div className='row d-flex justify-content-center'><h3>Base Stat</h3></div>
            <Bar data={data} options={options} />
        </>
    );
}

export default Pokemonstat;
